module.exports = function () {
    var element = document.createElement('h1');

    element.innerHTML = 'Hello World';
    

    var AnswerRadioInput = Reace.createClass({
        render: function () {
            return (
                <div className="radio">
                    <label>
                        <input type="radio" /> Label Text
                    </label>
                </div>
            );
        }
    });

    ReactDOM.render(<AnswerRadioInput />, document.getElementById('container'));
    return element;
};



